<template>
  <div>
    <div class="transaction-bank-account-title">
      {{ bankAccountData.name }}
    </div>
    <div class="transaction-bank-account-subtitle">
      {{ bankAccountData.balance }}€
    </div>
    <div class="create-transaction-container">
      <TransactionCreateEdit :docId="0" :elData="''" @reloadTransactionsList="getTransactionsList" />
    </div>
    <div class="bank-account-list-container">
      <div v-for="k in Object.keys(transactionsList)" :key="k" class="bank-account-element">
        <div class="transaction-title">
          {{ transactionsList[k].name }}
          <TransactionCreateEdit :docId="k" :elData="transactionsList[k]" @reloadTransactionsList="getTransactionsList" />
        </div>
        <div class="transaction-data-element">
          <div :class="[Number(transactionsList[k].amount) < 0 ? 'red-text' : 'green-text']">
            {{ transactionsList[k].amount }}€
          </div>
          |
          <div>
            {{ activitiesList[transactionsList[k].activityId].name }}
          </div>
          |
          <div>
            {{ transactionsList[k].date }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TransactionCreateEdit from "@/components/TransactionCreateEdit.vue"
import { mapGetters } from 'vuex';
import { collection, where, query, getDocs } from "firebase/firestore";

export default {
  name: 'TransactionList',
  components: {
    TransactionCreateEdit,
  },
  data() {
    return {
      transactionsList: {},
    }
  },
  computed: {
    ...mapGetters(["db", "bankAccountId", "bankAccountData", "activitiesList"]),
  },
  methods: {
    async getTransactionsList() {
      const q = query(collection(this.db, "transactions"), where("bankAccountId", "==", this.bankAccountId));

      const querySnapshot = await getDocs(q);
      querySnapshot.forEach((doc) => {
        // doc.data() is never undefined for query doc snapshots
        this.transactionsList[doc.id] = doc.data();
      });
    }
  },
  mounted() {
    this.getTransactionsList();
  },
}
</script>

