<template>
  <div>
    <div class="create-account-container" @click="openModal" v-if="docId == 0">
      <div class="create-account-btn"></div>
      Add new Activity
    </div>
    <div class="edit-btn" @click="openModal" v-else></div>
    <dialog class="modal-container" :id="docId + '-modal'">
      <div class="close-btn-container">
        <div class="close-btn" @click="closeModal"></div>
      </div>
      <div class="input-container">
        <label for="ba-name">Name:</label>
        <input id="ba-name" type="text" v-model="activityName">
      </div>
      <div class="create-btn" @click="createEditActivity">Create</div>
    </dialog>
  </div>
</template>

<script>
import { doc, setDoc, updateDoc, collection } from "firebase/firestore"; 
import { mapGetters } from 'vuex';

export default {
  name: 'ActivitiesEdit',
  props: ["docId", "name"],
  data() {
    return {
      activityName: null,
    }
  },
  computed: {
    ...mapGetters(["db", "uid"]),
  },
  methods: {
    openModal() {
      this.activityName = this.name;
      const modalEl = document.getElementById(this.docId + "-modal");
      modalEl.showModal();
    },
    closeModal() {
      const modalEl = document.getElementById(this.docId + "-modal");
      modalEl.close();
    },
    async createEditActivity() {
      if(this.docId == 0) {
        const newId = this.uid + Date.now().toString();
        await setDoc(doc(this.db, "activities", newId), {
          name: this.activityName,
        }).then(_ => {
          this.$emit("reloadActivitiesList");
          this.closeModal();
        }).catch(e => {
          console.log(e)
        })
      } else {
        const bankAccountRef = doc(this.db, "activities", this.docId);
        await updateDoc(bankAccountRef, {
          name: this.activityName,
        }).then(_ => {
          this.$emit("reloadActivitiesList");
          this.closeModal();
        }).catch(e => {
          console.log(e)
        })
      }
    },
  },
}
</script>

