<template>
  <div>
    <div class="create-account-container">
      <ActivitiesCreateEdit :docId="0" :name="''" @reloadActivitiesList="getActivitiesList" />
    </div>
    <div class="bank-account-list-container">
      <div v-for="k in Object.keys(activitiesList)" :key="k" class="bank-account-element">
        <div class="bank-account-title">
          {{ activitiesList[k].name }}
          <ActivitiesCreateEdit :docId="k" :name="activitiesList[k].name" @reloadActivitiesList="getActivitiesList" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ActivitiesCreateEdit from "@/components/ActivitiesCreateEdit.vue"
import { mapGetters, mapMutations } from 'vuex';
import { collection, query, getDocs } from "firebase/firestore";

export default {
  name: 'ActivitiesList',
  components: {
    ActivitiesCreateEdit,
  },
  data() {
    return {
      activitiesList: {},
    }
  },
  computed: {
    ...mapGetters(["db",]),
  },
  methods: {
    ...mapMutations(["setActivitiesList"]),
    async getActivitiesList() {
      const q = query(collection(this.db, "activities"));

      const querySnapshot = await getDocs(q);
      querySnapshot.forEach((doc) => {
        // doc.data() is never undefined for query doc snapshots
        this.activitiesList[doc.id] = doc.data();
      });

      this.setActivitiesList(this.activitiesList);
    }
  },
  mounted() {
    this.getActivitiesList();
  },
}
</script>

